import * as React from "react";
import type { HeadFC } from "gatsby";

import { Link, navigate } from "gatsby";
import classNames from "classnames";
import AppContext from "../../context/AppContext";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import styled from "styled-components";
import Dropdown from "react-bootstrap/Dropdown";
import { WrappedParse } from "../../vendor/ParseApp";
import Spinner from "react-bootstrap/Spinner";
const ICON = styled.div`
  height: 32px;
  width: 32px;
  background-color: #f5f5f5;
  margin-right: 14px;
`;

const CustomToggle = React.forwardRef(({ children, onClick }, ref) => (
  <a
    href=""
    ref={ref}
    onClick={(e) => {
      e.preventDefault();
      onClick(e);
    }}
  >
    {children}
    &#x25bc;
  </a>
));

const IndexPage = ({ children }) => {
  const [isLoading, setIsLoading] = React.useState(false);
  const [contextState, setContextState] = React.useState({
    theme: "test111",
    showLoading: (show) => {
      setIsLoading(show);
    },
  });
  const user = WrappedParse?.User?.current();
  const isAuthenticated = !!user;
  return (
    <AppContext.Provider value={contextState}>
      <main>
        <div className="navbar navbar-expand-lg navbar-light bg-brand-1 d-flex mb-5">
          <Container>
            <div className="d-flex flex-row">
              <div className="circle-avatar me-2 defaultCursor">
                <span className="initials">{"JC"}</span>
              </div>
              <div className="d-flex justify-content-center  align-items-center">
                <h3>
                  <Link to="/">Jarvis</Link>
                </h3>
              </div>
            </div>
            <div>
              {"" && (
                <input
                  type="text"
                  className="form-control bg-brand-5 color-brand-6"
                  id="exampleFormControlInput1"
                  placeholder="Search In Web3"
                ></input>
              )}
            </div>

            <div className="text-uppercase d-flex flex-direction-row gap-6 color-white gap-3">
              {}

              {isAuthenticated ? (
                <Dropdown>
                  <Dropdown.Toggle
                    className="color-brand1"
                    as={CustomToggle}
                    variant="success"
                    id="dropdown-basic"
                  >
                    {user.get("name")}
                  </Dropdown.Toggle>

                  <Dropdown.Menu>
                    <Dropdown.Item as="div">
                      {" "}
                      <Link className="color-brand-1" to="/start">
                        My Applications
                      </Link>
                    </Dropdown.Item>

                    <Dropdown.Item as="div">
                      {" "}
                      <Link className="color-brand-1" to="/search">
                        Search Applications
                      </Link>
                    </Dropdown.Item>

                    <Dropdown.Item as="div">
                      {" "}
                      <Link className="color-brand-1" to="/account">
                        Profile
                      </Link>
                    </Dropdown.Item>
                    <Dropdown.Item
                      as="div"
                      onClick={() =>
                        WrappedParse?.User.logOut().then((e) => navigate("/"))
                      }
                    >
                      Logout
                    </Dropdown.Item>
                  </Dropdown.Menu>
                </Dropdown>
              ) : (
                <div className="defaultCursor">
                  <Link to="/login">Login</Link> |
                  <Link to="/signup"> Sign Up</Link>
                </div>
              )}
            </div>
          </Container>
        </div>

        <Container fluid className="position-relative">
          <Row>
            <Col xs={12} extraClass="col-page">
              {children}
              {isLoading && (
                <div class="spotlight">
                  <Spinner animation="border" role="status">
                    <span className="visually-hidden">Loading...</span>
                  </Spinner>
                </div>
              )}
            </Col>
          </Row>
        </Container>
      </main>
    </AppContext.Provider>
  );
};

export default IndexPage;

export const Head: HeadFC = () => <title>Home Page</title>;
